import axios from "axios";
import { history } from "../index";
import { isExpired } from "react-jwt";

export const USER_LOGIN = "userLogin";
export const TOKEN = "accessToken";
export const {
  saveStore,
  saveStoreJson,
  getStore,
  getStoreJson,
  removeStore,
  setCookie,
  getCookie,
  eraseCookie,
  getToken,
} = {
  getToken: () => {
    const token = localStorage.getItem("userLogin");
    if (token) return JSON.parse(token).accessToken;
    return null;
  },
  saveStore: (name, data) => {
    localStorage.setItem(name, data);
  },
  saveStoreJson: (name, dataJson) => {
    const data = JSON.stringify(dataJson);

    localStorage.setItem(name, data);
  },
  getStore: (name) => {
    if (localStorage.getItem(name)) {
      return localStorage.getItem(name);
    }
  },
  getStoreJson: (name) => {
    if (localStorage.getItem(name)) {
      return JSON.parse(localStorage.getItem(name));
    }
  },
  removeStore: (name) => {
    localStorage.removeItem(name);
  },
  setCookie: (name, value, days) => {
    var expires = "";
    if (days) {
      var date = new Date();
      date.setTime(date.getTime() + days * 24 * 60 * 60 * 1000);
      expires = "; expires=" + date.toUTCString();
    }
    document.cookie = name + "=" + (value || "") + expires + "; path=/";
  },
  getCookie: (name) => {
    var nameEQ = name + "=";
    var ca = document.cookie.split(";");
    for (var i = 0; i < ca.length; i++) {
      var c = ca[i];
      while (c.charAt(0) == " ") c = c.substring(1, c.length);
      if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length, c.length);
    }
    return null;
  },
  eraseCookie: (name) => {
    document.cookie =
      name + "=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;";
  },
};

//Cu hnh interceptor (Cu hnh cho tt request(gi i), Response(D liu nhn v))
export const http = axios.create({
  baseURL: `https://shop.cyberlearn.vn`,
  timeout: 30000,
});
//Cu hnh cho resquest u c token
http.interceptors.request.use(
  (config) => {
    config.headers = {
      ...config.headers,
      Authorization: `Bearer ${getToken()} `,
    };
    return config;
  },
  (err) => {
    return Promise.reject(err);
  }
);
//Cu hnh cho tt c cc response api
http.interceptors.response.use(
  (res) => {
    return res;
  },
  (err) => {
    //Bt li 400 hoc 404
    if (err.response?.status === 400 || err.response?.status === 404) {
      //Li do tham s => backend tr v 400 hoc 404 mnh s x l
      alert("tham s khng hp l hoc Trng email");
      //chuyn hng v home
      history.push("/");
    }

    if (err.response?.status === 401 || err.response.status == 403) {
      const isMyTokenExpired = isExpired(getStore(TOKEN));
      //token ht hn
      if (isMyTokenExpired) {
        alert("Ht phin ng nhp yu cu ng nhp li !");
        removeStore(TOKEN);
        removeStore(USER_LOGIN);
        //Chuyn hng trang dng f5
        window.location.href = "/login";
      }
      history.push("/login");
    }
    return Promise.reject(err);
  }
);
/* Cc status code thng gp
    200: Request gi i v nhn v kt qu thnh cng
    201: request gi i thnh cng v  c khi to 
    400: bad request => request gi i thnh cng tuy nhin khng tm thy d liu t tham s gi i
    404: Not found (Khng tm thy api ), hoc tng t 400
    401: Unauthorize token khng hp l khng c quyn truy cp vo api 
    403: Forbinden token hp l tuy nhin cha  quyn  truy cp vo api 
    500: Error server (Li xy ra trn server c kh nng l frontend gi d liu cha hp l dn n backend x l b li). Backend code li trn server ! => Test bng post man hoc swagger nu api khng li => front code sai, ngc li tail fail trn post man v swagger th bo backend fix.

*/
